@import url("https://fonts.googleapis.com/css2?family=Paytone+One&display=swap");

h1,
h2,
.hs,
.btn {
  font-family: "Paytone One", sans-serif;
}
p {
  text-align: center;
}

.center {
  display: flex;
  align-items: center;
  justify-content: center;
  align-content: center;
  justify-items: center;
  height: 100vh;
}

label {
  color: #fae1dd;
}

button {
  cursor: pointer;
}

a {
  color: black;
}
.hs {
  color: #fae1dd;
  margin: 0;
  display: flex;
  font-weight: 400;
  text-align: center;
  max-width: 650px;
  font-size: 1.5rem;
}

.buttons {
  display: flex;
  gap: 0.5rem;
  justify-content: center;
  margin: 1rem 0;
  flex-wrap: wrap;
}

.footer-img {
  width: 100%;
  position: fixed;
  /* z-index: -1; */
  bottom: 0;
}

.app {
  height: 100vh;
}
